h2, h3 {
    display: none;
}
.container {
    width: 100%;
}
.encadre {
    box-shadow: none;
    width: 100%;
    border: none;
    //background-color: #f3f3f3 !important;
    padding: 0;
    margin: 0;

    > div {
        text-align: left;
        margin: 0 !important;
        padding: 0;
        margin-bottom: 10px !important;

        label {
            text-align: left !important;
            font-weight: 600;
        }
    }
    input[type=submit] {
        background: #ff6c00 !important;
        border: none;
        font-weight: normal;
        padding: 7px 70px !important;
        margin-top: 18px;

        &:hover {
            border: none !important;
        }
    }
    .text-center:last-child {
        text-align: right;
    }
    .control-label {
        width: 310px !important;
        padding-left: 0;

        + div {
            width: calc(100% - 310px);
            padding: 0;

            input, select {
                max-width: 100% !important;
                text-align: left;
            }
        }
    }
    &.form-horizontal .input-group {
        max-width: 100% !important;

        input {
            max-width: 100% !important;
            text-align: left;
        }

    }
}

@media (max-width: 768px) {

    .encadre .control-label+div {
        width: 100%;
    }
}
